import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { OutgoingRoutingModule } from './outgoing-routing.module';
import { OutgoingComponent } from '../outgoing.component';
import { DataTablesModule } from 'angular-datatables';
import { ReactiveFormsModule } from '@angular/forms';
import { IncomingComponent } from '../../incoming/incoming.component';
import { IncomingModule } from '../../incoming/incoming/incoming.module';


@NgModule({
  declarations: [OutgoingComponent],
  imports: [
    CommonModule,
    OutgoingRoutingModule,
    DataTablesModule,
    ReactiveFormsModule,
    IncomingModule
  ]
})
export class OutgoingModule { }
